.social-media-icon {
  color: black;
  font-size: 3rem;
}

.social-media-wrapper {
  grid-row: 2/-1;
  grid-column: 5/6;
  justify-items: center;
  display: grid;
  align-self: flex-start;
  grid-template-rows: auto 2rem;
  grid-template-columns: repeat(3, auto);
  width: 100%;
  max-width: 20rem;
}

.social-media-info {
  grid-row: 2;
  grid-column: 1/-1;
  align-self: center;
  justify-self: center;
}

@media (max-width: 1000px) {
  .social-media-icon {
    font-size: 5vw;
  }
  .social-media-mobile-wrapper {
    grid-row: 1/-1;
    grid-column: 7/-1;
    align-self: flex-end;
    justify-self: center;
    width: 100%;
    display: none;
    grid-template-rows: auto;
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 760px) {
  .social-media-info {
    display: none;
  }
  .social-media-icon {
    font-size: 2rem;
  }
}